
import Bus from '../eventbus'
import XTable from 'vux/src/components/x-table/index.vue'

export default {
name: 'ddd',
components: {
XTable
},
data () {
return {
msg: 'Welcome to ddd',
data: [],
rawdata: [],
more: true,
page: 0,
toastext: '',
toasttime: 800,
toastwidth: 'auto',
showToast: false,
toastP: 'top',
pagedata: '',
}
},
created () {
let vm = this;

Bus.$on('pagedata', vm.datarestore);
Bus.$on('page', data => this.page = data);

vm.$ajax.get('static/data.json')
.then(function(res){
res.data.forEach(function(item, index){
item.time = getLocalTime(item.time);
})

vm.getGroup(res.data, function(res){
if(!vm.pagedata){
vm.data = res;
}
});
})
.catch(function(err){
console.log('err: ',err);
})

},
watch: {
pagedata() {
this.data = this.pagedata
}
},
methods: {

toastFn (toastP, text) {
this.toastP = toastP
this.showToast = true
this.toastext = text
},

getGroup (val, callback) {
let vm = this;
let arr = []; 
for(var i=0; i<val.length; i++){
if((i/10)%1 === 0){ // 10的倍数

arr.push([]);
for(var j=i; j<val.length; j++){
if(j < (i+10)){   // 按每10个一组

arr[arr.length-1].push(val[j]);
}
}

}
}
console.log('arr: ',arr);

vm.rawdata = arr;

callback(arr[0]);
},

loadmore () {



if(this.page+1 >= this.rawdata.length){
console.log('暂无数据');
this.toastFn('top', '暂无数据');
this.more = false;
}else{


this.page++;
for(var i=0; i<this.rawdata[this.page].length; i++){
this.data.push(this.rawdata[this.page][i]);
}
}
},

detail (name) {

this.$router.push(`/list_state/list_detail?name=${name}`);
},

datarestore (pagedata) {
let vm = this;
vm.pagedata = pagedata;
console.group();
console.log('before_data: ',vm.data);
vm.data = [];
for(var i=0; i<pagedata.length; i++){
vm.data.push({
name: pagedata[i].name,
time: pagedata[i].time,
city: pagedata[i].city,
age: pagedata[i].age
})
}
console.log('after_data: ',vm.data);
console.groupEnd();
},
getStore (data){
console.log('data: ',data);
}
},
beforeDestroy () {
Bus.$emit('pagedata', this.data);
Bus.$emit('page', this.page);
},
destroyed () {
Bus.$off('pagedata');
Bus.$off('page');
}
}



